<template>
    <div class="v-container">
        <mt-header title="--">
             <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="v-field-group">
            <mt-field label="" placeholder="" v-model="username"></mt-field>
            <mt-field label="" placeholder=" " v-model="email" type="email"></mt-field>
            <mt-field label="" placeholder="" v-model="tel" type="tel"></mt-field>
            <mt-field label="" placeholder="" type="number" v-model="age"></mt-field>
            <mt-cell title="">
                <select v-model="gender" style="width: 250px;margin-left:12px;">
                    <option value="0"></option>
                    <option value="1"></option>
                </select>
            </mt-cell>
            <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
            <mt-field label="" placeholder="" type="password" v-model="rpassword"></mt-field>
        </div>
        <div class="v-btn-group">
            <mt-button @click.native="clickReg" type="primary" size="large">&nbsp;</mt-button>
            <p style="font-size:12px;">or HR<router-link to="/registercom" style="color:#26a2ff;">HR</router-link></p>
            <router-link to="/login">
            <mt-button size="large" plain>&nbsp;</mt-button>
            </router-link>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Login',  
    data() {
        return {
            username: '',
            email: '',
            tel: '',
            age: '',
            gender: 0,
            role: 0,
            motto: '',
            password: '',
            rpassword: ''
        }
    },
    methods: {
        clickReg(){
            this.$store.dispatch('register', {
                name: this.username,
                number: this.tel,
                email: this.email,
                gender: this.gender,
                motto: this.motto,
                password: this.password,
                rpassword: this.rpassword,
                role: this.role,
                age: this.age
            })
        }
    }
}
</script>
<style lang="less" scoped>
.v-field-group {
    margin-top: 10px;
}

.v-btn-group {
    padding: 20px 10px 10px 10px;
    & > * {
        margin-bottom: 10px;
    }
}
</style>



