<template>
    <div class="v-container">
        <mt-header title=""></mt-header>
        <div class="info-part">
            <mt-cell title=""></mt-cell>
            <mt-field label="" placeholder="" ></mt-field>
            <mt-field label="" placeholder="" ></mt-field>
            <mt-field label="" placeholder="" ></mt-field>
            <mt-field label="" placeholder="" ></mt-field>
        </div>
        <div class="info-part">
            <mt-cell title=""></mt-cell>
            <mt-field label="" placeholder="" type="textarea" ></mt-field>
        </div>
       
        <div class="info-part">
            <mt-button plain @click="clickCancel">&nbsp;</mt-button>
            <mt-button type="primary" @click="clickSave">&nbsp;</mt-button>
            <mt-button type="danger" @click="clickDelete">&nbsp;</mt-button>
        </div>
    </div>
</template>
<script>

import { MessageBox } from 'mint-ui'

export default {
    name: 'Resume',
    data() {
        return {

        }
    },
    methods: {
        clickCancel(){
            MessageBox.confirm('').then(action => {
                this.$router.push('/employment')
            }, action => {})
        },
        clickSave(){
            MessageBox.alert('')
        },
        clickDelete(){
            MessageBox.confirm('').then(action => {
                this.$router.push('/employment')
            }, action => {})
        }
    }
}
</script>
<style lang="less" scoped>
.info-part {
    margin-top: 10px;
    &:last-child{
        display: flex;
        button{
            flex: 1;
            margin: 10px;
        }
    }
}
</style>



