<template>
    <div class="v-container">
        <mt-header title="">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="v-field-group">
            <mt-field label="" placeholder="" v-model="username"></mt-field>
            <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
            <mt-cell title="">
                <select v-model="role" style="width: 250px;">
                    <option value="0"></option>
                    <option value="1"></option>
                </select>
            </mt-cell>
        </div>
        <div class="v-btn-group">
            <mt-button @click.native="clickLogin" type="primary" size="large">&nbsp;</mt-button>
            <p style="font-size:12px;">?</p>
            <router-link to="/register">
                <mt-button size="large" plain>&nbsp;</mt-button>
            </router-link>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Login',
    data() {
        return {
            username: '',
            password: '',
            role: 0
        }
    },
    methods: {
        clickLogin() {
            this.$store.dispatch('login', {
                name: this.username,
                password: this.password,
                role: this.role
            })
        }
    }
}
</script>
<style lang="less" scoped>
.v-field-group {
    margin-top: 10px;
}

.v-btn-group {
    padding: 20px 10px 10px 10px;
    & > * {
        margin-bottom: 10px;
    }
}
</style>



