<template>
    <div class="v-container">
        <mt-header title=""></mt-header>
        <div class="info-part">
            <mt-field label="" placeholder="" v-model="title"></mt-field>
            <mt-field label="" placeholder="" type="number" v-model="salaryLow"></mt-field>
            <mt-field label="" placeholder="" type="number" v-model="salaryHigh"></mt-field>
            <mt-field label="" placeholder="" v-model="region"></mt-field>
            <mt-cell title="">
                <select v-model="education" style="width: 250px;">
                    <option value="0"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                </select>
            </mt-cell>
            <mt-field label="" placeholder="" v-model="yearWork"></mt-field>
            <mt-field label="" placeholder="" type="textarea" v-model="description" rows="4"></mt-field>
        </div>
        <div class="info-part">
            <mt-button plain @click="clickCancel">&nbsp;</mt-button>
            <mt-button type="primary" @click="clickSave">&nbsp;</mt-button>
        </div>
    </div>
</template>
<script>
import {
    MessageBox
} from 'mint-ui'

export default {
    name: 'Resume',
    data() {
        return {
            title: '',
            salaryLow: '',
            salaryHigh: '',
            region: '',
            education: 1,
            description: '',
            yearWork: ''
        }
    },
    methods: {
        clickCancel() {
            MessageBox.confirm('').then(action => {
                this.$router.go(-1)
            }, action => {})
        },
        clickSave() {
            let userId = this.$store.state.user.userid
            this.$store.dispatch('publishJob', {
                userId,
                title: this.title,
                salaryLow: this.salaryLow,
                salaryHigh: this.salaryHigh,
                region: this.region,
                education: this.education,
                description: this.description,
                yearWork: this.yearWork
            })
        }
    }
}
</script>
<style lang="less" scoped>
.info-part {
    margin-top: 10px;
    &:last-child {
        display: flex;
        button {
            flex: 1;
            margin: 10px;
        }
    }
}
</style>



