<template>
    <div class="v-container">
        <mt-header title="--HR">
             <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="v-field-group">
            <mt-field label="" placeholder="" v-model="username"></mt-field>
            <mt-field label="" placeholder="" v-model="comname"></mt-field>
            <mt-field label="" placeholder="" type="url" v-model="comurl"></mt-field>
            <mt-field label="" placeholder=" " v-model="email" type="email"></mt-field>
            <mt-field label="" placeholder="" v-model="tel" type="tel"></mt-field>
            <mt-field label="" placeholder="" v-model="address"></mt-field>
            <mt-field label="" placeholder="" type="textarea" rows="4" v-model="introduction"></mt-field>
            <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
            <mt-field label="" placeholder="" type="password" v-model="rpassword"></mt-field>
        </div>
        <div class="v-btn-group">
            <mt-button @click.native="clickRegCom" type="primary" size="large">&nbsp;</mt-button>
            <p style="font-size:12px;">or <router-link to="/register" style="color:#26a2ff;"></router-link></p>
            <router-link to="/login">
            <mt-button size="large" plain>&nbsp;</mt-button>
            </router-link>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Login',  
    data() {
        return {
            username: '',
            comname: '',
            comurl:'',
            email: '',
            tel: '',
            address: '',
            role: 1,
            introduction: '',
            password: '',
            rpassword: ''
        }
    },
    methods: {
        clickRegCom(){
            this.$store.dispatch('registerCom', {
                username: this.username,
                name: this.comname,
                tel: this.tel,
                url: this.comurl,
                email: this.email,
                address: this.address,
                introduction: this.introduction,
                password: this.password,
                rpassword: this.rpassword,
                role: this.role
            })
        }
    }
}
</script>
<style lang="less" scoped>
.v-field-group {
    margin-top: 10px;
}

.v-btn-group {
    padding: 20px 10px 10px 10px;
    & > * {
        margin-bottom: 10px;
    }
}
</style>



