<template>
    <div class="v-container">
        <mt-header title=""></mt-header>
        <div class="info-part">
            <mt-cell title=""></mt-cell>
            <mt-field label="" placeholder="" v-model="username"></mt-field>
        </div>

        <div class="info-part">
            <mt-cell title=""></mt-cell>
            <mt-field label="" placeholder="" v-model="comName"></mt-field>
            <mt-field label="" placeholder="" type="email" v-model="email"></mt-field>
            <mt-field label="" placeholder="" type="tel" v-model="tel"></mt-field>
            <mt-field label="" placeholder="" v-model="place"></mt-field>
            <mt-field label="" placeholder="" type="textarea" rows="4" v-model="comDetail"></mt-field>
        </div>

        <div class="info-part">
            <mt-button plain @click="clickCancel">&nbsp;</mt-button>
            <mt-button type="primary" @click="clickSave">&nbsp;</mt-button>
        </div>
    </div>
</template>
<script>

import { MessageBox } from 'mint-ui'

export default {
    name: 'ComSettings',
    data() {
        return {
            comName: '',
            comDetail: '',
            username: '',
            email: '',
            tel: '',   
            place: '',
            result: [{
                title: 'web',
                label: '',
                value: '10k-20k'
            }, {
                title: 'Java',
                label: '',
                value: '10k-20k'
            }, {
                title: 'PHP',
                label: '',
                value: '10k-20k'
            }]
        }
    },
    methods: {
        clickCancel(){
            MessageBox.confirm('').then(action => {
                this.$router.push('/userinfo')
            }, action => {})
        },
        clickSave(){
            MessageBox.alert('')
        }
    }
}
</script>
<style lang="less" scoped>
.info-part {
    margin-top: 10px;
    &:last-child{
        display: flex;
        button{
            flex: 1;
            margin: 10px;
        }
    }
}
</style>



